.notifications .notification.show-enter.show-enter-active {
  right: 10px;
  transition: all 250ms ease-in-out;
}

.notifications .notification.show-leave {
  right: 10px;
}

.notifications .notification.show-leave.show-leave-active {
  right: -300px;
  transition: all 250ms ease-in-out;
}

.sidebar-menu {
  padding: 0;
  margin: 0;
  list-style: none;
}

.sidebar-menu li.item {
  display: block;
  margin: 0;
  padding: 0;
}

.sidebar-menu li.item.item-group .items {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
}

.sidebar-menu li.item.item-group .items li.item a {
  padding-left: 67px;
}

.sidebar-menu li.item.item-group .items li.item.active > a {
  font-weight: 600;
}

.sidebar-menu li.item a {
  display: block;
  padding: 10px 30px;
  color: #ACC2E0;
  transition: 250ms background-color ease-in-out;
  position: relative;
  text-decoration: none;
}

.sidebar-menu li.item a .material-icons {
  vertical-align: middle;
  margin-right: 10px;
}

.sidebar-menu li.item a span {
  display: inline-block;
  line-height: 24px;
  vertical-align: middle;
}

.sidebar-menu li.item a:hover {
  background-color: #1A1A1A;
}

.sidebar-menu li.item a:before {
  display: block;
  width: 4px;
  height: 100%;
  position: absolute;
  content: " ";
  top: 0;
  left: 0;
  bottom: 0;
  transition: 250ms background-color ease-in-out;
}

.sidebar-menu li.item.open .items {
  display: block;
}

.sidebar-menu li.item.active {
  background-color: #1A1A1A;
}

.sidebar-menu li.item.active a {
  color: #ACC2E0;
  background-color: #1A1A1A;
}

.sidebar-menu li.item.active a:before {
  background-color: #3498DB;
}

.sidebar-menu li.item.active > a {
  font-weight: 600;
}



